
.myfooter {
  z-index: -1;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  margin-top: 20px;
  width: 100%;
  height: 100px;
  background-color: #202020;
  vertical-align: middle;
}
.myfooter .footer-wrapper {
  height: 80px;
  width: 500px;
  margin: auto auto;
  padding-top: 1rem;
}
.myfooter .footer-wrapper .footer-text-up {
  margin-top: 5px;
  text-align: center;
  display: block;
  opacity: 0.64;
  font-weight: 300;
  font-size: 0.92rem;
  letter-spacing: 0.02rem;
  line-height: 1.72rem;
  color: #f5f5f5;
}
.myfooter .footer-wrapper .line-wrapper {
  height: 2px;
  width: 100%;
  border-bottom: 1px solid #f5f5f5;
}
.myfooter .footer-wrapper .footer-text-down {
  display: block;
  opacity: 0.64;
  text-align: center;
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: 0.02rem;
  line-height: 1.72rem;
  color: #f5f5f5;
}


